.project-container-main {
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.shape5 {
  display: flex;
  justify-content: space-evenly;
  clip-path: circle(45% at 50% 50%);
  width: 3%;
  height: 5vmin;
  background-color: rgb(156, 233, 133);
  transition: all 0.7s ease-in-out;
  animation: bounce2 2s infinite;
}

.shape5.hovered {
  animation: none;
  padding: 2%;
  height: auto;
  width: 50%;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  background: linear-gradient(to bottom, rgba(129, 67, 243, 0.123), rgba(78, 10, 109, 0.055));

}

.shape5 .label  {
  display: none;
}

.shape5.hovered .label {
  display: unset;
}

.project-main-login {
display: flex;
flex-direction: column;
text-align: center;
width: 100%;
}

.main-login {
display: flex;
flex-direction: column;
align-items: center;
gap: 2em;
}

.login, .password {
display: flex;
flex-direction: column;
align-items: center;
gap: 1em;
border-bottom: 1px solid rgba(156, 255, 153, 0.418);
width: 35%;
}

input  {
border: none;
background-color: inherit;
text-align: center;
font-family: monospace;
color: rgb(195, 255, 200);
}

input[type="text"]:focus, input[type="password"]:focus {
outline: none;
background-color: inherit;
width: 100%;
color: rgb(195, 255, 200);
}

.login:focus-within label, .password:focus-within label {
transform: translateY(-10px);
}
.login label {
display: flex;
justify-content: center;
color: rgb(195, 255, 200);
transition: all 0.3s ease;

}

.password label{
display: flex;
justify-content: center;
color: rgb(195, 255, 200);
transition: all 0.3s ease;
}

.main-btn{
border: none;
background-color: inherit;
color: rgb(159, 231, 165);
width: 5em;
font-family: monospace;  
box-shadow: 0 0 10px rgba(188, 250, 193, 0.4);
}

.main-button {
display: flex;
flex-direction: column;
align-items: center;
gap: 1em;
}

.log-fail{
display: flex;
flex-direction: row;
border: none;
background-color: inherit;
color: rgb(231, 179, 159);
width: 15em;
font-family: monospace;  
box-shadow: 0 0 10px rgba(188, 250, 193, 0.4);
opacity: 0; 
animation: loginFailed 2s forwards; 
}


.main-btn:hover {
animation: lightGlow 1.5s infinite alternate;
}

@keyframes loginFailed {
0% {
 opacity: 0;
}
50% {
  opacity: 1;
}
100% {
  opacity: 0;
}
}

@media (max-width: 900px) {
  

  .shape5{
    animation: none;
    padding: 2%;
    height: auto;
    width: 80%;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    background: linear-gradient(to bottom, rgba(129, 67, 243, 0.123), rgba(78, 10, 109, 0.055));
    padding: 5%;
  }

  .shape5 .label  {
    display: unset;
  }
  

}